
.floating-button-wrapper {
  position: absolute;
  left: calc(50% - 50px);
  top: calc(50% - 20px);
}

.btn {
  display: block;
  width: 100px;
  height: 50px;
  padding: 15px;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: inherit;
  background: inherit;
  border-collapse: separate;
  box-shadow:
    3px 3px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    3px 3px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    3px 3px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-transition: -webkit-box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: -webkit-box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  -o-transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out, -webkit-box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.btn:hover {
  color: rgba(255, 126, 126, 0.5);
  -webkit-transform: translateY(-1px);
      -ms-transform: translateY(-1px);
          transform: translateY(-1px);
  border-collapse: separate;
  box-shadow:
    4px 4px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    4px 4px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    4px 4px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
}

.btn:active {
  color: rgb(255, 126, 126);
  -webkit-transform: translateY(1px);
      -ms-transform: translateY(1px);
          transform: translateY(1px);
  border-collapse: separate;
  box-shadow:
    2px 2px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -webkit-box-shadow:
    2px 2px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
  -moz-box-shadow:
    2px 2px 0px,
    -1px -1px 0,  
    1px -1px 0,
    -1px 1px 0,
    1px 1px 0;
}

@media only screen and (max-width:768px) {
  .btn.active {
    color: rgb(255, 126, 126);
    -webkit-transform: translateY(1px);
        -ms-transform: translateY(1px);
            transform: translateY(1px);
    border-collapse: separate;
    box-shadow:
      2px 2px 0px,
      -1px -1px 0,  
      1px -1px 0,
      -1px 1px 0,
      1px 1px 0;
    -webkit-box-shadow:
      2px 2px 0px,
      -1px -1px 0,  
      1px -1px 0,
      -1px 1px 0,
      1px 1px 0;
    -moz-box-shadow:
      2px 2px 0px,
      -1px -1px 0,  
      1px -1px 0,
      -1px 1px 0,
      1px 1px 0;
  }
}


